import React from 'react';

import { Button, Modal } from 'antd';

class AppWorks extends React.Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <div id="videos" className="block worksBlock">
        <div className="headerflex">
          <div className="titleHolder">
            <h2>Videos</h2>
            <p>Perspiciatis vero similique, ut ducimus modi ipsam autem tempora</p>
          </div>
          <div className="contentHolder">
            <Button size="large" onClick={this.showModal}><i className="fas fa-play"></i></Button>
          </div>
          <Modal
            title="Woocommerce Tutorial"
            visible={this.state.visible}
            onCancel={this.handleCancel}
            footer={null}
            destroyOnClose = {true}
            width={1000}
          >
            <iframe title="Tutorial" width="100%" height="550" src="https://www.youtube.com/embed/yWQR2pdmBQw"></iframe>
          </Modal>
        </div>
      </div>
    );
  }
}

export default AppWorks;